@import (reference) '../../themes/societyadmin/semantic/src/definitions/globals/reset';
@import (reference) '../../themes/societyadmin/semantic/src/definitions/globals/site';


.ui.grid .photo.column {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: center;

  &.selected .photo.image img {
    box-sizing: border-box;
    border: 0.2em solid @blue;
    margin: -0.05em;
  }

  .ui.photo.segment {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;

    padding: 0;

    img {
      box-shadow: 0 2px 4px 0 rgba(34, 36, 38, .12), 0 2px 10px 0 rgba(34, 36, 38, .08);
    }

    .photo.image {
      min-height: 100px;
      display: flex;
      align-items: center;
    }

    .photo.title {
      align-self: center;
    }
  }
}
